:root {
--color:black;  
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    /* width: 100px; */
    width: 20rem;
    border-radius: 15px;
    margin: 1.3rem;
    border: 2px solid black;
    padding: 2rem;
    transition: width 0.2s;
}
h2{
    text-align: center;
}
.container:hover{
    background-color: black;
    color: white;
    --color:blue;

    /* padding: 2.4rem; */
    /* margin: 1.7rem; */
    width: 21.2rem;
    cursor: pointer;
}
.img{
    width: 100%;
    margin-top:0.9rem ;
}
.container:hover .img{
    border: 2px solid white;
}
.flex{
    display: flex;
    justify-content: space-between;
    margin-top: 0.9rem;
}

