.flex_header{
    display: flex;
    flex-direction:row;
    justify-content: center;
    gap:3rem;
    align-items: center;
    width: 100%;
    margin: 1.3rem;
}
.flex_header button{
    padding: .9rem;
    border-radius: 9px;
    border: none;
    background-color: blue;
    color: white;
    cursor: pointer;
    font-weight: bold;
}
